import React, { createRef, useEffect, RefObject } from 'react';
import { useState} from 'react';
import { TopBar, Logo, StyledNavLink, Nav} from './NavBar.style';
import logo_top from '../../images/logo.png'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import styled, { keyframes, css } from 'styled-components';
import COLORS from '../../colors/colors';

interface NavBarProps {
    className?: string;
}

const MenuIcon = styled(FontAwesomeIcon)`
  color: white;
  font-size: 2rem;
`

const ShowOnMobile = styled.div`
  @media (min-width: 1024px) {
    display: none;
  }
`

const ShowOnDesktop = styled.div`
  @media (max-width: 1024px) {
    display: none;
  }
`

const MobileMenu = styled.div`
  color: white;
  padding-left: 1rem;
  padding-right: 1rem;
  text-align: center;
  height: 100%;
  width: 100px;
  line-height: 70px;
  background-color: ${COLORS.gray};
`

interface MobileMenuContainerProps {
  isHidden: boolean,
}

const fadeIn = css`
  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
`

const fadeOut = css`
  @keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
`

const FadeIn = styled.div`
  ${fadeIn}
  animation: fadeIn .2s;
`

const MobileMenuContainer = styled.div<MobileMenuContainerProps>`
  width: 14rem;
  position: absolute;
  top: 70px;
  right: 0;
  ${fadeIn}
  ${fadeOut}
  
  animation: ${props => props.isHidden ? 'fadeOut' : 'fadeIn'} .2s;
  visibility: ${props => props.isHidden ? 'hidden' : 'visible'};
  transition: visibility .2s;

`





const MobileMenuLink = styled(StyledNavLink)`
  width: 100%;
`

const NavBar: React.FC<NavBarProps> = (props: NavBarProps) => {

  const [isHidden, setIsHidden] = useState(true);
  const mobileMenuContainerRef = createRef<HTMLDivElement>();

  useEffect(() => {
    console.log('component did mount');
    document.addEventListener("mousedown", hideMenuOnOutsideClick);
    return () => {
      document.removeEventListener("mousedown", hideMenuOnOutsideClick)
    };
  }, []);

  const toggleMenu = () => setIsHidden(!isHidden);
  

  const hideMenuOnOutsideClick = (event: MouseEvent): void => {
    if(isHtmlElementClicked(mobileMenuContainerRef, event)) {
      console.log('skipping :)')
      return;
    }
    console.log('hiding')
    //  setIsHidden(true);
  }

  const isHtmlElementClicked = (referenceToElement: RefObject<HTMLDivElement>, event: MouseEvent): boolean => {
    let currentReference = referenceToElement.current;
    if(currentReference) {
      return currentReference.contains(event.target as HTMLElement);
    }
    return false;
  }
    

  return (
    <TopBar>
      <Logo><img src={logo_top} height={62} /></Logo>
      <Nav>
        <ShowOnDesktop>
          <StyledNavLink to="/">Strona domowa</StyledNavLink>
          <StyledNavLink to="/offer">Oferta</StyledNavLink>
          <StyledNavLink to="/prices">Cennik</StyledNavLink>
          <StyledNavLink to="/employees">Pracownicy</StyledNavLink>
          <StyledNavLink to="/contact">Kontakt</StyledNavLink>
        </ShowOnDesktop>
        <ShowOnMobile>
          <MobileMenu onClick={toggleMenu} >
            <MenuIcon icon={faBars} />
          </MobileMenu>
          <MobileMenuContainer isHidden={isHidden} ref={mobileMenuContainerRef}>
            <MobileMenuLink to="/">Strona domowa</MobileMenuLink>
            <MobileMenuLink to="/offer">Oferta</MobileMenuLink>
            <MobileMenuLink to="/prices">Cennik</MobileMenuLink>
            <MobileMenuLink to="/employees">Pracownicy</MobileMenuLink>
            <MobileMenuLink to="/contact">Kontakt</MobileMenuLink>
          </MobileMenuContainer>
        </ShowOnMobile>
      </Nav>
    </TopBar>
  );
}
  
export default NavBar;